@font-face {
  font-family: 'FuturaPT';
  src: url('../fonts/FuturaPT-Bold.woff') format("woff");
  font-weight: bold;
}
@font-face {
  font-family: 'FuturaPT';
  src: url('../fonts/FuturaPT-Medium.woff') format("woff");
  font-weight: 500;
}
@font-face {
  font-family: 'FuturaPT-Demi';
  src: url('../fonts/FuturaPT-Demi.woff') format("woff");
  font-weight: 500;
}
* {
  all: unset;
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
  flex: none;
}
*::before,
*::after {
  box-sizing: border-box;
  flex: none;
}
head,
script {
  display: none;
}
html {
  height: 100%;
  font-size: 1px;
}
@media (max-width: 1200px) {
  html {
    font-size: 0.8px;
  }
}
body {
  height: 100%;
  font-size: 16rem;
  font-family: FuturaPT;
  color: #37334d;
  line-height: 1.25;
}
a[href]:hover,
button:hover,
a[href]:focus,
button:focus {
  cursor: pointer;
}
p {
  -webkit-hyphens: auto;
  -ms-hyphens: auto;
  hyphens: auto;
  margin-bottom: 0.5em;
}
h2 {
  text-align: center;
  font-weight: bold;
  font-size: 36rem;
  margin-bottom: 30rem;
}
h3 {
  font-family: FuturaPT-Demi;
  font-size: 26rem;
  margin-bottom: 20rem;
}
main {
  flex-grow: 1;
}
.row {
  flex-direction: row;
}
body header {
  box-shadow: 0 2px 4px 0 rgba(225, 225, 225, 0.5);
}
body footer {
  box-shadow: inset 0 1px 3px 0 rgba(190, 190, 190, 0.5);
}
body header,
body footer {
  padding-top: 30rem;
  background-color: #ffffff;
}
body header .row > *,
body footer .row > * {
  flex-direction: row;
  align-items: center;
  margin-bottom: 30rem;
}
@media (max-width: 1200px) {
  body header .row > *:nth-child(-n+2),
  body footer .row > *:nth-child(-n+2) {
    justify-content: center;
  }
}
body header nav,
body footer nav {
  justify-content: space-around;
  font-size: 20rem;
}
body header .number,
body footer .number {
  flex-direction: column;
  align-items: flex-end;
}
@media (max-width: 1200px) {
  body header .number,
  body footer .number {
    flex-direction: row;
    align-items: center;
    justify-content: space-around;
  }
}
body header .number span,
body footer .number span {
  font-family: FuturaPT-Demi;
  font-size: 28rem;
  color: #3140ae;
  margin-bottom: 0.5em;
}
body header .number .recall,
body footer .number .recall {
  font-size: 16rem;
  color: #6b6b6b;
  text-decoration: underline;
}
body section {
  padding-top: 60rem;
  padding-bottom: 60rem;
}
.btn {
  width: 100%;
  max-width: 350px;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  text-align: center;
  color: white;
  font-size: 20rem;
  font-weight: bold;
  padding: 15rem 35rem;
  border-radius: 30px;
  cursor: pointer;
  background-image: linear-gradient(to top, #2948ff, #396afc);
}
.btn:hover,
.btn:active,
.btn:focus {
  background-image: linear-gradient(to top, #041ca9, #023cea);
}
.logo img {
  width: 100%;
  max-width: 161px;
  height: auto;
  max-height: 60px;
}
.remark {
  color: #a1a1a1;
}
.plus {
  flex-direction: row;
