.clients {
  background-color: #f7faff;
}
#about li {
  flex-wrap: wrap;
  align-items: center;
  margin-bottom: 50rem;
}
#about .left {
  text-align: right;
  font-size: 70rem;
  font-weight: bold;
  color: #3742fa;
}
@media (max-width: 1200px) {
  #about .left {
    font-size: 50rem;
  }
}
#about .right {
  max-width: 310px;
  text-align: left;
  font-size: 24rem;
}
#cities {
  font-size: 24rem;
}
.mask {
  position: relative;
  padding-bottom: 56.25%;
  overflow: hidden;
  border-radius: 15px;
  box-shadow: 0 2px 4px 0 rgba(190, 190, 190, 0.5);
  background-color: #f7faff;
  margin-bottom: 30rem;
}
.mask > * {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.img {
  align-self: center;
  margin-bottom: 30rem;
}
.img img {
  align-self: flex-start;
}
img[alt="img"] {
  width: 100%;
  height: auto;
}
@media (max-width: 768px) {
  img[alt="img"] {
    display: none;
  }
}
.popup {
  display: none;
  justify-content: center;
  z-index: 3;
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  background-color: rgba(0, 0, 0, 0.2);
}
.popup.active {
  display: flex;
}
.popup .form {
  border-radius: 20px;
  box-shadow: 0 2px 7px 0 rgba(196, 196, 196, 0.5);
  padding-top: 75rem;
  padding-bottom: 75rem;
}
@media (max-width: 768px) {
  .popup .form {
    padding-top: 50rem;
    padding-bottom: 50rem;
  }
}
.popup .close {
  position: absolute;
  right: 30rem;
  top: 30rem;
  width: 15px;
  height: 15px;
  opacity: 0.5;
  cursor: pointer;
}
.popup .close:hover,
.popup .close:focus {
  opacity: 1;
}
.popup .close:before,
.popup .close:after {
  position: absolute;
  content: ' ';
  height: 2px;
  width: 20px;
  background-color: #9b9b9b;
}
.popup .close:before {
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%) rotate(45deg);
  transform: translate(-50%, -50%) rotate(45deg);
}
.popup .close:after {
  top: 50%;
  right: 50%;
  -webkit-transform: translate(50%, -50%) rotate(-45deg);
  transform: translate(50%, -50%) rotate(-45deg);
}
.form {
  flex-basis: auto;
  position: relative;
  align-self: center;
  background-color: white;
}
.form .btn {
  align-self: center;
}
.form label {
  margin-bottom: 10px;
}
.form label:last-of-type {
  margin-bottom: 30rem;
}
.form label span {
  font-size: 14rem;
  font-family: FuturaPT-Demi;
  color: #7f8fa4;
  margin-bottom: 5px;
}
.form input {
  padding: 15px;
  font-size: 16rem;
  border: 1px solid #dfe3e9;
  border-radius: 4px;
}
.form input:focus {
  border-color: #2ea2f8;
}
ul.arrow li {
  flex-direction: row;
  align-items: center;
  margin-bottom: 1em;
}
ul.arrow li:before {
  content: '';
  width: 9px;
  height: 14px;
  background-image: url("data:image/svg+xml;charset=utf-8,%3Csvg width='9' height='14' xmlns='http://www.w3.org/2000/svg'%3E%3Cg fill='none' fill-rule='evenodd'%3E%3Cpath fill='%23FFF' d='M-165-5136h1440V523H-165z'/%3E%3Cpath fill='%233742FA' d='M.5 1.87L5.714 7 .5 12.13l1.393 1.37L8.5 7 1.893.5z'/%3E%3C/g%3E%3C/svg%3E");
  margin-right: 10px;
}
ul.cities {
  flex-direction: row;
  flex-wrap: wrap;
}
ul.cities li {
  flex-direction: row;
  align-items: center;
  margin-bottom: 5rem;
  margin-right: 20rem;
}
ul.cities li:before {
  content: '';
  width: 9px;
  height: 12px;
  background-image: url("data:image/svg+xml;charset=utf-8,%3Csvg width='9' height='12' xmlns='http://www.w3.org/2000/svg'%3E%3Cg fill='none' fill-rule='evenodd'%3E%3Cpath fill='%23FFF' d='M-185-3051h1440v5659H-185z'/%3E%3Cpath d='M4.5 6a1.5 1.5 0 1 1 0-3 1.5 1.5 0 0 1 0 3m0-6C2.015 0 0 1.88 0 4.2c0 3 4.5 7.8 4.5 7.8S9 7.2 9 4.2C9 1.88 6.985 0 4.5 0' fill='%233742FA'/%3E%3C/g%3E%3C/svg%3E");
  margin-right: 10px;
}
b {
  font-size: 36px;
  font-weight: bold;
  margin-bottom: 20rem;
}
#cases .item {
  font-size: 18rem;
  border-radius: 15px;
  box-shadow: 0 3px 4px 0 rgba(190, 190, 190, 0.5);
  background-color: #ffffff;
  padding: 20rem;
  margin-bottom: 40rem;
}
#cases .item:hover,
#cases .item:focus {
  box-shadow: 0 6px 14px 4px rgba(190, 190, 190, 0.5);
}
#cases .item img {
  align-self: center;
  max-width: 310px;
  padding: 10px;
}
#cases .item ul {
  font-size: 16rem;
}
#cases .item .profit {
  align-items: center;
  margin-top: 20rem;
}
#cases .item .profit p {
  font-size: 20rem;
}
#cases .item .profit b {
  font-size: 26px;
  font-weight: bold;
}
#cases .item .profit b:hover,
#cases .item .profit b:focus {
  color: #2d50fe;
}
#map {
  width: 100%;
  height: 510rem;
  background: grey url(../img/map.png) center center no-repeat;
  background-size: contain;
  padding: 0;
}
@media (max-width: 1200px) {
  #map {
    height: 250px;
  }
}
#contacts {
  font-size: 24px;
}
#contacts h3 {
  font-family: FuturaPT;
  font-size: 26px;
  font-weight: 500;
  margin-bottom: 20rem;
}
#small_map {
  width: 100%;
  height: 500px;
  background: grey url(../img/small_map.png) center center no-repeat;
  background-size: contain;
}
@media (max-width: 1200px) {
  #small_map {
    height: 240px;
  }
}
.map,
.map * {
  display: block;
}
